export default () => {
	let inputs = document.querySelectorAll('.input_digit-pretify');

	let mask = (value) => {
		let output = [];

		for (let i = 0; i < value.length; i++) {
			if (i !== 0 && i % 4 === 0) {
				output.push(' ')
			}
			output.push(value[i]);
		}

		return output.join[''];
	}

	let unmask = (value) => {
		var output = value.replace(new RegExp(/[^\d]/, 'g'), '');
		return output;
	}
}


